import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { models } from "../../components/Admin/AdminData";
import { get_model_data } from "../../redux/actions/models";

const mapState = ({ auth }) => ({
    user: auth.user,
    permissions: auth.permissions,
});

function AllData() {
    const { category } = useParams();
    const { user, permissions } = useSelector(mapState);
    const [model, setModel] = useState(null);
    const [modelData, setModelData] = useState([]);
    const dispatch = useDispatch();
    const [choosedItems, setChoosedItems] = useState([]);
    const [choosedAll, setChoosedAll] = useState(false);
    const [otherModelData, setOtherModelData] = useState(null);

    const handleAddAll = () => {
        if (choosedItems.length === modelData.length) {
            let choosed = [];
            setChoosedItems([...choosed]);
            setChoosedAll(false);
        } else {
            let choosed = modelData.map((data) => {
                return data?.id;
            });
            setChoosedItems([...choosed]);
            setChoosedAll(true);
        }
    };
    const handelToggleItem = (id) => {
        if (choosedItems.find((foundId) => foundId === id)) {
            const reduced = choosedItems.filter((item) => item !== id);
            setChoosedItems([...reduced]);
        } else {
            setChoosedItems([...choosedItems, id]);
        }
    };
    useEffect(() => {
        setModel(models.find((model) => model.slug === category));
    }, [category]);
    useEffect(() => {
        if (model) {
            dispatch(get_model_data(model?.url)).then((res) => {
                // console.log("Result", res);
                setModelData(res.results);
                setOtherModelData({
                    next: res?.next,
                    prev: res?.previous,
                    count: res?.count,
                });
            });
        }
    }, [model]);

    return (
        <div>
            <div className="text-black main-title  p-4">
                {model?.name} ({otherModelData?.count})
            </div>
            <div className="row">
                <div className="row col-lg-6 mb-3 ms-2">
                    <div className="col-auto dropdown">
                        <button
                            type="button"
                            className="dropdown-toggle truncate-overflow-1 badge   border me-2 my-1 subcat-badge border"
                            data-bs-toggle="dropdown"
                            style={{
                                outline: "none",
                                borderRadius: "10px",
                                padding: "0.60rem 0.90rem",
                                fontSize: "18px",
                                fontWeight: "500",
                                boxShadow:
                                    "0 10px 30px 0 rgba(68, 55, 77, 0.2)",
                            }}
                            aria-expanded="false"
                        >
                            <i
                                class="bi bi-filter-right me-2 "
                                style={{ fontSize: "24px" }}
                            ></i>{" "}
                            Filter
                        </button>
                        <ul
                            class="dropdown-menu  "
                            style={{ borderRadius: "15px", overflow: "hidden" }}
                            aria-labelledby="dropdownMenuButton1"
                        >
                            <li>
                                <a
                                    class="dropdown-item p-2 list-group-item border-top-0"
                                    href="#"
                                >
                                    Action
                                </a>
                            </li>
                        </ul>
                    </div>

                    <div className="col-auto dropdown">
                        <button
                            type="button"
                            className="dropdown-toggle truncate-overflow-1 badge   border me-2 my-1 subcat-badge border"
                            data-bs-toggle="dropdown"
                            style={{
                                outline: "none",
                                borderRadius: "10px",
                                padding: "0.60rem 0.90rem",
                                fontSize: "18px",
                                fontWeight: "500",
                                boxShadow:
                                    "0 10px 30px 0 rgba(68, 55, 77, 0.2)",
                            }}
                            aria-expanded="false"
                        >
                            <i
                                class="bi bi-sort-alpha-down me-2 "
                                style={{ fontSize: "24px" }}
                            ></i>{" "}
                            Order
                        </button>
                        <ul
                            class="dropdown-menu"
                            aria-labelledby="dropdownMenuButton1"
                        >
                            <li>
                                <a class="dropdown-item" href="#">
                                    Action
                                </a>
                            </li>
                            <li>
                                <a class="dropdown-item" href="#">
                                    Another action
                                </a>
                            </li>
                            <li>
                                <a class="dropdown-item" href="#">
                                    Something else here
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="row col-lg-6 mb-3 justify-content-end">
                    {permissions &&
                        permissions.find(
                            (perm) => perm === model?.addPermission
                        ) && (
                            <div className="col-auto ">
                                <button
                                    className="btn btn-primary p-2  "
                                    style={{ borderRadius: "10px" }}
                                >
                                    <i class="bi bi-plus-lg me-2"></i>
                                    <strong>Add </strong>
                                </button>
                            </div>
                        )}
                    {permissions &&
                        permissions.find(
                            (perm) => perm === model?.deletePermission
                        ) && (
                            <div className="col-auto ">
                                <button
                                    className="btn btn-danger p-2 "
                                    disabled={choosedItems.length === 0}
                                    style={{ borderRadius: "10px" }}
                                >
                                    <i class="bi bi-x-lg me-2"></i>
                                    <strong>Delete </strong>
                                </button>
                            </div>
                        )}
                </div>
            </div>
            <div class="container" style={{ width: "90%" }}>
                <div class="table-responsive">
                    <table
                        class="table "
                        style={{
                            borderRadius: "15px",
                            borderCollapse: "collapse",
                        }}
                    >
                        <thead className="border-0 text-black">
                            <tr className="border-0">
                                <th scope="row" className="border-0">
                                    <label class="control control--checkbox">
                                        <input
                                            type="checkbox"
                                            checked={choosedAll}
                                            onChange={() => handleAddAll()}
                                        />
                                        <div class="control__indicator"></div>
                                    </label>
                                </th>
                                {model?.fields.map((field) => {
                                    return (
                                        <th scope="col" className="border-0">
                                            {field.name}
                                        </th>
                                    );
                                })}
                            </tr>
                        </thead>
                        <tbody
                            className="bg-black text-black"
                            style={{
                                borderRadius: "10px !important",
                                boxShadow: "rgba(0, 0, 0, 0.5) 15px 5px 10px",
                            }}
                        >
                            {Array.isArray(modelData) &&
                                modelData.length >= 1 &&
                                modelData.map((row) => {
                                    // console.log(row["category"]["name"]);
                                    return (
                                        <tr>
                                            <th scope="row">
                                                <label class="control control--checkbox">
                                                    <input
                                                        type="checkbox"
                                                        checked={choosedItems.includes(
                                                            row?.id
                                                        )}
                                                        onChange={() =>
                                                            handelToggleItem(
                                                                row?.id
                                                            )
                                                        }
                                                    />
                                                    <div class="control__indicator"></div>
                                                </label>
                                            </th>
                                            {model.fields.map((field) => {
                                                if (field?.type === "image") {
                                                    return (
                                                        <td>
                                                            <img
                                                                className="img-fluid "
                                                                style={{
                                                                    borderRadius:
                                                                        "15px",
                                                                }}
                                                                src={
                                                                    row[
                                                                        field.id
                                                                    ]
                                                                }
                                                                alt="product"
                                                            />
                                                        </td>
                                                    );
                                                }
                                                if (field?.type === "tick") {
                                                    let ok = row[field?.id];
                                                    return (
                                                        <td>
                                                            {ok === true ? (
                                                                <i class="bi bi-check-lg h5 text-success"></i>
                                                            ) : (
                                                                <i class="bi bi-dash-circle-fill h5 text-danger"></i>
                                                            )}
                                                        </td>
                                                    );
                                                }

                                                if (field?.subId) {
                                                    if (row[field?.id]) {
                                                        let x = row[field?.id];
                                                        for (const [
                                                            key,
                                                            value,
                                                        ] of Object.entries(
                                                            x
                                                        )) {
                                                            if (
                                                                key ===
                                                                field?.subId
                                                            )
                                                                return (
                                                                    <td>
                                                                        {value}
                                                                    </td>
                                                                );
                                                        }

                                                        return;
                                                    }
                                                }
                                                return (
                                                    <td>{row[field?.id]}</td>
                                                );
                                            })}
                                        </tr>
                                    );
                                })}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}

export default AllData;
